import React, {useEffect} from 'react';
import SiteContainer from 'views/Container/Container.js';
import styled from 'styled-components';
import {Link} from 'gatsby';
import {sendError} from 'utilities/Measure.js';
import { globalHistory } from "@reach/router"
import {log} from 'utilities/Measure.js';

//globalHistory.location.pathname
const NotFoundPage = () => {
  useEffect(()=>{
    let timeout = setTimeout(()=>{sendError(`404 url ${globalHistory.location.pathname}`, false, WEB);}, 1000);
    return ()=>{
      clearTimeout(timeout)};
  }, [globalHistory.location.pathname])
  return (
      <SiteContainer>
        <NotFound>
          <h1>NOT FOUND</h1>
          <p>This page doesn't exist. <Link to={'/'} >Here's</Link> one that does!</p>
        </NotFound>
      </SiteContainer>
  )
}

export default NotFoundPage

const NotFound = styled.div`
  background-color: grey;
  max-height: 70vh;
  width: 90%;
  margin: auto;
  max-width: 600px;
  text-align: center;
  padding: 10px;
  overflow-y: auto;
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
`